.palette {
  grid-template-columns: repeat(5, 20%);
  position: absolute;
  z-index: 10;
  background-color: white;
  padding: 5px;
  border-top: 7px solid darkslateblue;
  border-radius: 4px;
  -webkit-box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.3);
  top: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  display: none; }
  .palette.active {
    display: grid; }
  .palette:before {
    content: '';
    display: block;
    position: absolute;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    top: -14px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    border-bottom: 7px solid darkslateblue; }
  .palette button {
    padding: 0;
    margin: 4px;
    background: transparent;
    position: relative;
    width: 13px;
    height: 13px; }
    .palette button span {
      width: 13px;
      height: 13px;
      margin: 0;
      border: 1px solid black;
      display: block;
      -webkit-transition: 0.1s;
      -o-transition: 0.1s;
      transition: 0.1s; }
    .palette button:hover span {
      width: 20px;
      height: 20px;
      z-index: 10;
      margin: -3px 0 0 -3px; }



